@import url(/src/vendor/fonts/fonts.css);


.main {
  padding: 0 20px;
  font-family: "CabinetGrotesk-Bold", arial, sans-serif;
  font-weight: 700;
  font-size: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
  padding: 0 20px;
  font-family: "CabinetGrotesk-Medium", arial, sans-serif;
  font-weight: 700;
  font-size: 20px;
}

.header__logo {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;
}

.header__avatar-logo {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;
}

.header__button {
  font-family: "CabinetGrotesk-Medium", arial, sans-serif;
  font-weight: 700;
  font-size: 20px;
  border: 0;
  padding: 0px 0px 0px 0px;
  background-color: transparent;
}

.weather {
  position: relative;
}

.weather_info {
  position: absolute;
  top: 20px;
  left: 16px;
  color: white;
  font-size: 36px;
}

.weather_image {
  width: 100%;
  height: 100%;
}

.item-card__section {
  margin-top: 20px;
}

.item-card__image {
  width: 325px;
  height: 328px;
  border-radius: 10px;
}

.item-card__name {
  text-align: center;
  font-family: "CabinetGrotesk-Bold", arial, sans-serif;
  font-weight: 700;
  font-size: 20px;
}

.item-card__items {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  margin-top: 20px;
}


.footer {
  display: flex;
  justify-content: space-between;
  margin-top: 171px;
  margin-bottom: 24px;
  padding: 0 20px;
  font-family: "CabinetGrotesk-Medium", arial, sans-serif;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal__container {
  background: white;
  max-width: 496px;
  width: 100%;
  border-radius: 20px;
  padding: 34px 36px;
  font-family: "CabinetGrotesk-Medium", arial, sans-serif;
  position: relative;
}

.modal__close {
  position: absolute;
  background: url(/se_project_react/static/media/newclosebutton.26da5474.svg);
  background-color: transparent;
  background-size: contain;
  bottom: calc(90% + 5%);
  left: calc(90% + 5%);
  width: 20px;
  height: 20px;
  border: none;
}

.modal__close[id="close-garment-modal"] {
  position: absolute;
  background: url(/se_project_react/static/media/newclosebutton.26da5474.svg);
  background-color: transparent;
  background-size: contain;
  bottom: calc(83% + 5%);
  left: calc(85% + 5%);
  width: 20px;
  height: 20px;
  border: none;
}

.modal__form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.modal__label {
  margin-bottom: 8px;
}

.modal__input {
  border-top: 0;
  border-right: 0;
  border-left: 0;
  padding-left: 0px;
  margin-bottom: 24px;
}
/* uncomment this VVVV when you need to add the style needed to the span tags*/

/* .modal__span {
  color: red;
} */

.modal__title {
  margin-bottom: 24px;
}

.modal__button {
  margin-top: 32px;
  padding-top: 8px;
  padding-right: 16px;
  padding-bottom: 8px;
  padding-left: 16px;
  border-radius: 4px;
  background-color: black;
  font-family: "CabinetGrotesk-Medium", arial, sans-serif;
  color: white;
  border: none;
  transition: ease-in-out 0.5s;
  cursor: pointer;
}

.modal__button:hover {
  opacity: 0.6;
}

.modal__button:disabled {
  opacity: 0.3;
}


